import React, { Component } from 'react';
import { fakeAuth } from '../Login/login'
import { Link } from "react-router-dom";
import './home.css';

class Home extends Component {

    render() {

        const hello = fakeAuth.isAuthenticated ?
            (
                <div className='authen-complete'>
                    <h1> {fakeAuth.userName} </h1> <br />
                    <Link to='/createpost'></Link>
                </div>
            ) :
            (
                <div className='authen-complete'>
                    <h1></h1>
                </div>
            );
        const newsTitle = '';
        const news = (
            <div className='news'>
                <Link className='title' to={`/post/${newsTitle}`}></Link>
                <div className='preview'>
                     
                     
                     
                    
                </div>
                {/*<Link className='extra' to={`/post/${newsTitle}`}> ...</Link>*/}
                <div className='user-post-name'>
                    Siwakorn Ruenrit
                </div>
            </div>
        );
        const hotTopic = (
            <div className='hot-news'>
                <div className="left-half">
                    <img className='post-image' src='https://assets.change.org/photos/2/ab/gf/gEABgFWkjGDxumE-800x450-noPad.jpg?1522729038' alt='' />
                </div>
                <div className="right-half">
                    <Link className='title' to={`/post/${newsTitle}`}></Link>
                    <p>
                         
                         
                         
                        
                    </p>
                    <div className='user-post-name'>
                        Siwakorn Ruenrit
                    </div>
                </div>
            </div>
        );

        return (
            <div>
                <div className='home-container'>
                    {hello}
                    <h2>...</h2>
                    {hotTopic}
                    <h2>...</h2>
                    {news}
                    {news}
                    {news}
                    {news}
                </div>
            </div>
        );
    }
}

export default Home;


